import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { graphql } from "gatsby";
import { SiteHead, Intro, SiteHeader } from "../components";
import { withTheme } from "../theme";

const IndexPage = ({
  data: {
    site: {
      siteMetadata: { author, description }
    },
    allChannelsYaml,
    file
  } = {}
}) => (
  <Fragment>
    <SiteHead />
    <SiteHeader />
    <Intro
      author={author.name}
      description={description}
      channels={allChannelsYaml.edges.map(({ node }) => node)}
      fluidAvatar={file.childImageSharp.fluid}
    />
  </Fragment>
);

IndexPage.propTypes = {
  data: PropTypes.shape({
    site: PropTypes.any,
    allChannelsYaml: PropTypes.any,
    file: PropTypes.any
  }).isRequired
};

export const query = graphql`
  query HomeQuery {
    site {
      siteMetadata {
        author {
          name
        }
        description
      }
    }
    allChannelsYaml(limit: 3) {
      edges {
        node {
          id
          title
          username
          url
          icon
        }
      }
    }
    file(relativePath: { eq: "avatars/original.jpg" }) {
      id
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;

export default withTheme(IndexPage);
